.nav {
	grid-area: nav-bar;
	background-color: rgb(41, 36, 36);
	padding: 20px 0px 0px 35px;
	border-radius: 15px;
	letter-spacing: 1px;
	font-size: 19px;
}

.nav div {
	margin: 0px 0px 15px 0px;
}

.nav a {
	display: block;
	border-bottom: 1px solid #ffffff;
}

.active {
	color: rgb(134, 216, 219);
}